.wp-block-sojourn-menu-item {
	// background-color: #21759b;
	// color: #fff;
	// padding: 2px;

  color: var(--green);
  margin-bottom: 3rem;
}

.menu-item__heading {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  margin-bottom: 0.5rem;
}

.menu-item__heading h3 {
  color: var(--green);
  font-weight: 700;
  font-size: 1.25rem;
  font-family: var(--work);
  margin-bottom: 0;
}

.menu-item__heading span {
  font-size: 1rem;
  color: var(--green);
  font-weight: 700;
  margin-left: 1rem;
}

.wp-block-sojourn-menu-item p {
  margin-bottom: 0.5rem;
}

.wp-block-sojourn-menu-item p:last-child {
  margin-bottom: 0.5rem;
}

.menu-item__categories {
  display: flex;
}

.menu-item__category {
  margin-right: 14px;
}

.menu-item__category img {
  height: 18px;
  margin-bottom: 0;
}
